html, body, #root {
  margin: 0;
  font-family: "Iansui094-Regular";
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  padding: 2.5% 0% 2.5% 0%;
  height: 100%;
  box-sizing: border-box;
}

.text-center {
  text-align: center;
}

.app-container {
  padding: 0%;
  border: 0px solid #b7b7b7;
  border-radius: 5px;
  box-sizing: border-box;
  /* max-width: 500px; */
  max-height: 100%;
  height: 100%;
  overflow: scroll;
  overflow-x: hidden;
}

.render-container{
  margin: 0 auto;
  max-width: 800px;
  padding-bottom: 200px;
}

/* width */
::-webkit-scrollbar {
  width: 2px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 8px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
#App .context {
    max-width: 800px;
    min-width: 600px;
    margin: 0 auto;
    position: relative;
}

#App h1 {
    font-size: 32px;
}

@media screen and (max-width: 768px) {
    #App .context {
        width: 100%;
        margin: 0 auto;
        position: relative;

    }
}

@media screen and (max-width: 576px) {
    #App .context {
        margin-left: 0px;
        box-sizing: border-box;
        max-width: 576px;
        min-width: 380px;
        position: relative;
    }
}
#Post-Block{
    padding: 15px;
    margin-bottom: 50px;
    border: #d8d8d8 solid 1px;
    border-radius: 5px;
}

#Post-Block hr{
    border: 1px solid #d8d8d8;
}

#Post-Block .title-badge{
    width: 100px;
    margin-left: 10px;
    position: relative;
    top: 4px;
}

#Post-Block a.post-title{
    font-size: 18px;
}

@media screen and (max-width: 768px) {
    #Post-Block{
        padding: 12px;
        max-width: 100%;     
    }
}

@media screen and (max-width: 576px) {
    #Post-Block{
        width: 100%;
        padding: 12px;
        box-sizing: border-box;
        margin: 0 auto;
        margin-bottom: 20px;
    }
}
#Search-Bar {
    margin-bottom: 32px;
}

#Search-Bar input[type=text] {
    border-color: #b7b7b7;
    border-style: solid;
    border-width: 0px 0px 1px 0px;
    height: 24px;
    padding-left: 5px;
    margin-right: 5px;
}

#Search-Bar input[type=text]:focus{
    outline-width: 0px;
}

#Search-Bar button[type=submit]{
    height: 28px;
    background-color: #e3e3e3;
    border-color: #b7b7b7;
    border-style: solid;
    border-width: 0px 0px 0px 0px;
}
.md-preview h1{
    visibility: hidden;
    line-height: 0px;
}
#Loading{
    position: absolute;
    top: 10px;
    left: 10px;
}
#Corner-Menu {
    color: gray;
    font-size: 3px;
    position: fixed;
    bottom: 8%;
    right: 2.5%;
}

#Corner-Menu .link {
    height: 42px;
    width: 42px;
    border-radius: 50%;
    display: block;
    margin-top: 15px;
}

#Corner-Menu .link a {
    color: #b7b7b7;
}

#Corner-Menu .link a:hover {
    color: gray;
}

@media screen and (max-width: 768px) {
    #Corner-Menu {
        color: gray;
        font-size: 3px;
        position: fixed;
        bottom: 2%;
        right: 6%;
    }

    #Corner-Menu .link {
        height: 36px;
        width: 36px;
        border-radius: 50%;
        display: block;
        margin-top: 15px;
    }
}
#Footer{
    text-align: center;
    color: gray;
    font-size: 10pt;
    position: fixed;
    bottom: 2.5%;
    right: 2.5%;
}

#Footer a{
    color: #6699FF;
}

#Footer a:link{
    text-decoration: none;
}

@media screen and (max-width: 768px){
    #Footer{
        color: gray;
        font-size: 10pt;
        position: static;
        position: initial;
    }
}
